@import 'libs';
@import 'helpers/variables';
@import 'helpers/mixins';
@import 'general/fonts';
@import 'general/base';
@import '../blocks/blocks';

$light-color: #fff;
$accent-color: #65e49c;
$bg-light-color: #f5f6fa;
$bg-dark-color: #44444f;
$gray-color: #9191b5;

li{
	list-style-type: none;
}
h1, h2, h3, h4{
	margin: 0;
	padding: 0;
}

a {text-decoration: none;}

body {font-family: 'Roboto Slab', serif; font-size: 15px; line-height: 24px;}

small {font-weight: 400; font-style:  italic;}

h3 {font-size: 24px; letter-spacing: 1.2px;}
 .wrapper{
 	position: relative;
 }
 .wrap{
 	max-width: 1170px;
 	margin: 0 auto;
	padding: 38px 15px 166px 15px;
 }

 .page-main{
	background-image: url(../img/bg.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	color: $light-color;
    padding-bottom: 156px;
 }

 .page{
 	padding-bottom: 120px;
	padding-top: 162px;
	position: relative;
	@media (max-width: 1020px) {padding-top: 120px; padding-bottom: 120px;}
	&-carousel{
		@media (max-width: 1260px) {background-color: #44444f;}	
	}
 	&_gray{
 		background-color: $bg-light-color;
 	}
 	&__title{
 		font-size: 48px;
 		text-align: center;
 		margin-bottom: 22px;
 		letter-spacing: 2px;

 	}
 	&__subtitle{
 		color: $accent-color;
 		text-align: center;
 		font-size: 18px;
 		letter-spacing: 0.8px;
 	}
 	&__svg{
 		position: absolute;
 		top: -100px;
 		z-index: -1;
 		min-height: 694px;
 		@media (max-width: 1260px) {display: none;}
 	}
 }

 .page-main>.wrap{
 	margin-top: 14px;
 }

 .header{
 	display: flex;
 	justify-content: space-between;
 	align-items: center;
 	max-width: 1170px;
 	margin: 0 auto;
 	padding-top: 50px;
 	padding-bottom: 166px;
 	@media (max-width: 920px) {flex-direction: column}
 	&__logo {
 		display: block;
		background-image: url(../img/logo.svg);
		width: 137px;
		height: 28px;
		background-repeat: no-repeat;
 	}
 	&-list{
 		list-style-type: none;
 		display: flex;
 		justify-content: flex-end;
 	    letter-spacing: 1px;
 		font-family: 'Open Sans', serif;
 		&__item{
 			margin-left: 22px;
 			padding-bottom: 5px;
 			padding-top:5px;
 			
 			transition: 0.3s;
 			&:hover{
 				border-bottom: 3px solid $accent-color;
 			}
 		}

 		&__item.active{
 			border-bottom: 3px solid $accent-color;
 		}

 		&__link {
 			color: $light-color;

 		}
 	}
 }

 .block-text{
 	flex-basis: calc(93% - 500px);
 	padding-left: 52px;
 	@media (max-width: 1020px){flex-basis: calc(93% - 315px)}
 	&__v2{
 		margin-left: 165px;
 	}
 	&__name{
 		font-family: 'Open Sans', serif;
 		font-size: 18px;
 		letter-spacing: 0.7px;
   		margin-bottom: 28px;
   		margin-left: 4px;
   		margin-top: 0;
 	}
	
