		margin-top: 16px;
		margin-left: 0px;
		margin-bottom: 20px;
 	}
	&__namev2{
		margin-top: -5px;
    	margin-left: 4px;
    	margin-bottom: 33px;
	}
 	&__title{
 		font-size: 48px;
 		line-height: 53px;
 		font-weight: 700;
 		letter-spacing: 1.8px;
 		margin-bottom: 38px;
 		span{
 			display: block;
 			color: $accent-color;
 			font-size: 18px;
 			line-height: 23px;
 			margin-top: 11px;
 			letter-spacing: 0.9px;
 			font-weight: 300;
 			margin-left: 3px;
 		}
 	}
 	&__description {
 		font-family: 'Open Sans', serif; 
 		font-size: 15px;
 		line-height: 24px;
 		letter-spacing: 0.75px;
 		margin-bottom: 24px;
 	}
 	&__btn{
 		display: block;
 		background: linear-gradient(to top left, #65e49c,  #65e4af);
		color: $light-color;
		max-width: 250px;
