		&::before{
			content: '';
		    display: block;
		    height: 20px;
		    width: 100%;
		    background-color: #b2f1be;
		    z-index: -1;
		    position: absolute;
		    bottom: -2px;
		    border-radius: 4px;
		    left: 0px;
    		}
		}
	&-item{
		margin-bottom: 69px;
		&__date{
			font-family: 'Open Sans', serif;
			margin-left: -1px;
    		letter-spacing: 0.8px;
    		margin-bottom: 5px;
		}
		&__title{
			font-size: 18px;
		    letter-spacing: 0.9px;
		}
		&__desc{
			font-family: 'Open Sans', serif;
    		line-height: 24px;
    		letter-spacing: 0.5px;
   			word-spacing: 1.6px;
   			margin-bottom: 36px;
		}
		&__btn{
			padding: 15px 32px;
    		border: none;
    		border-radius: 4px;
    		color: #fff;
            background: linear-gradient(to top left, #65e49c,  #65e4af);
    		position: relative;
    		font-size: 16px;
    		letter-spacing: 1.8px;
    		outline: none;
		}
	}
 }
 .block-book{
