    				background-image: url(../img/f-item-2.png);
    				width: 18px;
    				height: 18px;
    				position: absolute;
    				top: 5px;
    				left: 162px;
    			}
    			& .f-item-3{
    				display: block;
					background-image: url(../img/f-item-3.png);
    				width: 23px;
    				height: 18px;
    				position: absolute;
    				top: 5px;
    				left: 194px;
    			}	
    		}
		}
	}
	&-table{
		flex-basis: 585px;
		@media (max-width: 1020px){flex-basis: 100%; margin-top: 40px}
		&>h3{
			margin-bottom: 26px;
		}
		&__btn{
			background: linear-gradient(to top left, #65e49c,  #65e4af);
		    border: none;
		    color: #fff;
		    border-radius: 3px;
		    padding: 15px 46px;
		    margin-top: 29px;
		    font-size: 17px;
		    letter-spacing: 1px;
		    font-weight: 300;
		    position: relative;
		    outline: none;
	    	&::before{
    			content: '';
    			display: block;
    			height: 20px;
    			width: 100%;
    			background-color: #b2f1be;
    			z-index: -1;
    			position: absolute;
    			bottom: -2px;
    			border-radius: 4px;
    			left: 0px;
    		}
		}
		&__list{
			margin: 0;
			padding: 0;
			column-count: 2;
			counter-reset: list;
			column-gap: 19px;
			margin-bottom: 15px;
			
			&>li{
				font-family: 'Open Sans', serif;
				position: relative;
				padding-left: 37px;
				margin-bottom: 8px;
				counter-increment: list;
				list-style-type: none;
				&::before{
					content: counter(list) ". ";
				}

				&::after {
					font-family: 'Open Sans', serif;
					background-color: #01c9bc;
					font-size: 15px;
					color: #fff;
					top: 0px;
					padding: 0px 7px;
					left: 0;
					position:absolute;
					content: "+";
				}
			}
			&_next{
				counter-reset: list 10;
				position: relative;
				&::before{
					content: '';
				    width: 555px;		
    				height: 2px;
    				background-color: #01c9bc;
    				display: block;
    				position: absolute;
    				top: -13px;	
				}
			}
		}
	}
}
