.block-letter{
	display: flex;
	justify-content: space-between;
	@media(max-width:1020px){flex-direction: column; text-align: center}
	&-item{
		flex-basis: 49%;
		@media (max-width: 1020px){flex-basis: 100%; max-width: 500px;margin: 0 auto;}
	}
	&__title{
		font-size: 48px;
		margin-bottom: 23px;
		letter-spacing: 1.9px;
		color: #44444f;
		margin-top: 8px;
	}
	&__text{
		font-family: 'Open Sans', serif;
		font-size: 15px;
		letter-spacing: 0.75px;
		color: #44444f;
		@media(max-width: 1020px){margin-bottom:40px}
		
	}
	&__input{
		width: 100%;
		background-color: $bg-dark-color;
		border: none;
		color: $gray-color;
		padding: 17px 20px;
		box-sizing: border-box;
		border-radius: 3px;
		letter-spacing: 0.6px;
		outline: none;
		&:focus::placeholder {
  			transform: rotate(-720deg);
  			font-size: 10px;
  			transition: 1s;
		}
	}
	&__btn{
		background: linear-gradient(to top left, #65e49c, #65e4af);
		border: none;
		color: $light-color;
		text-decoration: none;
		border-radius: 3px;
		text-align: center;
		font-size: 18px;
		margin-top: 21px;
		padding: 11px 0 15px 0;
		letter-spacing: 0.7px;
		box-shadow: 0 -2px #adedbb inset;
		display:  block;
		width: 100%;
		outline: none;
		@media (max-width: 1020px){ max-width: 500px; margin: 0 auto 21px;}
	}
}
