.block-carousel {
	text-align: center;
	h3{
		color: $accent-color;
		margin-bottom: 4px;
	}
	&__desc{
		color: $gray-color;
		font-family: 'Open Sans', serif;
		max-width: 760px;
		margin: 0 auto;
		line-height: 27px;
		letter-spacing: 0.75px;
	}
	&__expert{
		margin-bottom: 32px;
	}
	&-bq{
		position: absolute;
		display: block;
		background-image: url(../img/bq.png);
		width: 61px;
		height: 51px;
		top: -2px;
		right: -15px;
	}
	&-ava{
		position: relative;
		width: 152px;
		height: 152px;
		display: block;
		margin: 0 auto 27px;
	}
	&-thumb {
		display: block;
		margin: 0 auto;
		position: relative;
		overflow: hidden;
		width: 152px;
		height: 152px;
		img{
			width: 152px;
			height: 152px;
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			-o-fit-object: cover;
		}
	}
}
.slick-dots {
	display: flex;
	justify-content: center;
	padding: 0;
	&>li {
		margin: 0 5px;

		&>button {
			outline: none;
			text-indent: -9999px;
			width: 8px;
			height: 8px;
			border: none;
			border-radius: 100%;
			background-color: $accent-color;
			font-size: 0;
			line-height: 0;
			cursor: pointer;
			padding: 5px;
		}
	}
	&>li.slick-active{
		&>button{
			width: 37px;
			border-radius: 10px;
			transition: 0.35s;
			transition-delay: 0.15s;
		}
	}
}
.block-letter__input::-webkit-input-placeholder       {text-indent: 0px;   transition: text-indent 0.3s ease;}
.block-letter__input::-moz-placeholder                {text-indent: 0px;   transition: text-indent 0.3s ease;}
.block-letter__input:-moz-placeholder                 {text-indent: 0px;   transition: text-indent 0.3s ease;}
.block-letter__input:-ms-input-placeholder            {text-indent: 0px;   transition: text-indent 0.3s ease;}
.block-letter__input:focus::-webkit-input-placeholder {text-indent: 500px; transition: text-indent 0.3s ease;}
.block-letter__input:focus::-moz-placeholder          {text-indent: 500px; transition: text-indent 0.3s ease;}
